@import "./ACommonStyles.scss";

.navbar-section {
    display: flex;
    justify-content: space-between;
    height: 70px;
    align-items: center;
}



.nav-ul {
    padding: 0px;
    margin-bottom: 0px;
}

.nav-ul li {
    display: inline-block;
    font-weight: 490;
}

.nav-element li{
    display: inline;
    margin: 8px;
    color: $primaryColor;
    font-weight: 550;
}

.nav-element li:hover{
font-weight: 610;
}

.activ-nav-element li {
    color: $primaryColor;
    text-decoration: underline;
    
}
