@import "./ACommonStyles.scss";

@media (max-width: 700px) {
    #searchingInNav {
        width: auto;

        button{
            margin:0;
            width: 4.5rem;

        }
    }
}
@media (min-width: 700px) {
    #searchingInNav {
        width: 35%;
    }
}

#searchingInNav {
    .nav-search-form {
        min-width: 200px;
        width: 70%;
        display: flex;
        align-content: center;
        justify-content: space-between;
        padding-top: 0.1rem;
        padding-bottom: 0.1rem;
        background-color: $primaryColorGrey;
        border-radius: 0.5vw;
       

        input {
            width: -webkit-fill-available;
            height: 100%;
            border: none;
            background-color: $primaryColorGrey;
        }

        input:focus {
            background-color: lightgrey;
            border: none;
        }

        button {
            padding: 0;
            width: 15%;
            background-color: $primaryColorGrey;
            border-radius: unset;
            border: none;
        }
    }
}

//media querry - mniejszy ekran:

#dropdown-basic {
    background-color: $primaryColor;
    -webkit-box-shadow: -2px 2px 21px -6px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: -2px 2px 21px -6px rgba(0, 0, 0, 0.75);
    box-shadow: -2px 2px 21px -6px rgba(0, 0, 0, 0.75);
    border: none;

    margin-top: 0;
    margin-left: 10px;
}

#dropdown-basic:hover {
    -webkit-box-shadow: -2px 2px 27px -5px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: -2px 2px 27px -5px rgba(0, 0, 0, 0.75);
    box-shadow: -2px 2px 27px -5px rgba(0, 0, 0, 0.75);
}
