@import "./ACommonStyles.scss";

header {
  color: $primaryColor;

  .header-top {
    margin-top: 1.5vh;
    padding-bottom: 1.5vh;
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid $primaryColorGrey;
    font-weight: 500;
    color: $primaryColor;

    @media (max-width: 833px) {
      .my-account-div {
        width: 100%;
        justify-content: flex-end;
      }
    }

    @media (min-width: 833px) {
      .my-account-div {
        width: 35%;
        justify-content: flex-start;
      }
    }
    .my-account-div {
      display: flex;
      font-size: small;

      span {
        margin: 2px;

        .chart-icon {
          margin-left: 1rem;
        }
      }

      label {
        margin-bottom: 0;
        select {
          border: none;
          background-color: white;
          font-size: small;
          font-weight: 500;
          color: $primaryColor;

          option {
            padding: 0;
          }
        }
      }
    }

    #country-select {
      margin-left: 20px;
    }
  }

  .header-bottom {
    display: flex;
    justify-content: space-between;
    border-bottom: 1px solid lightgray;
  }

  .lot-logo {
    background: url(https://airkiosk.lot.com/themes/airkiosk-v-2/assets/img/pl_PL.png);
    height: 76px;
    width: 116px;
    margin: 20px;
  }

  @media (max-width: 828px) {
    .my-reserv-div {
      width: 256px;
      justify-content: flex-end;
    }
  }

  @media (min-width: 828px) {
    .my-reserv-div {
      width: 35%;
      justify-content: flex-start;
    }
  }

  @media (max-width: 611px) {
    .plane-icon {
      width: 0;
      visibility: hidden;
    }
  }

  @media (min-width: 611px) {
    .plane-icon {
      height: 50px;
    }
  }
  .my-reserv-div {
    display: flex;
    align-items: center;
  }

  .reserv-number {
    display: flex;
    justify-content: space-between;
    font-weight: bold;
    align-items: center;
    margin-top: -0.3rem;

    a {
      color: $primaryColor;
      text-decoration-line: underline;
      font-size: xx-small;
    }
  }
}
