.carousel-caption {
    bottom: 30%;
    p {
        width: 30%;
        margin-left: 35%;
    }
}

.carousel-indicators {
    bottom: 3vw;

    li {
        width: 10px;
        height: 10px;
        border-radius: 50%;
    }
}

button {
    background-color: transparent;
    border-radius: 3px;
    border: 1px solid #ffffff;
    display: inline-block;
    color: #ffffff;
    font-size: 13px;
    padding: 6px 24px;
    text-decoration: none;
}

@media screen and (max-width: 1090px) {
    .carousel-slide {
        width: 90%;
        margin: auto;
    }

    .carousel-caption {
        bottom: 27%;
        padding-bottom: 0px;
        // background-color: green;
    }
}

@media screen and (max-width: 930px) {
    .carousel-indicators {
        visibility: hidden;
    }
    .carousel-caption {
        // background-color: red;
        bottom: 22%;
    }
}

@media screen and (max-width: 841px) {
    .carousel-caption {
        // background-color: yellow;

        p {
            margin-left: 15%;
            width: 70%;
            text-align: center;
            margin-bottom: 10px;
        }
        button {
            margin-top: 10px;
            padding: 0px;
        }
    }
}

@media screen and (max-width: 667px) {
    .carousel {
        min-width: 400px;
    }
    .carousel-caption {
        // background-color: goldenrod;
        bottom: 0%;
        height: 100%;
        width: 90%;
        left: 5%;

        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-content: space-around;
        align-items: center;
        padding-top: 0px;

        h3 {
            font-size: 1.1rem;
        }
        p {
            font-size: small;
            margin: 0em;
        }
        button {
            margin: 0px;
            width: 50%;
        }
    }

    .carousel-control-next {
        width: 5%;
    }
    .carousel-control-prev {
        width: 5%;
    }
}
